#root, body, html{
    height:100vh;
}
body{
    margin:0;
    background-color: whitesmoke;
}
*{
    box-sizing: border-box;
}
html{
    scroll-behavior: smooth;
}
.media{
    height: 250px;
}
.border{
    border: solid;
}
.fullHeightCard{
    height: 100%;
}
.card{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    border-bottom: 10px solid white;
}
.activeCard{
    border-bottom: 10px solid #22289a;
}
.grid{
    display: flex;
}
.details{
    display: flex;
    justify-content: space-between;
    margin: 20px;
}
.title{
    padding: 0 16px;
}
.cardActions{
    padding: 0 16px 8px 16px;
    display: flex;
    justify-content: space-between;
}
.container{
    padding: 0% 5%;
    width: 100%;
    margin: 0;
}
.cards{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    text-align: center;
    width: 100%;
    height: 100%;
    padding: 10%;
    border-radius: 10;
    color: white;
}
.infocard{
    display: flex;
    flex-direction: column;
    text-align: center;
}
.footer{
    text-align: center;
    position: fixed;
    left: 0;
    bottom: 0;
    color: black;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 120px;
}
.link{
    text-decoration: none;
    color: rgba(21, 101,192);
}
.image{
    margin-left: 20;
}
.cardi{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50%;
    padding: 3%;
    border-radius: 10;
    color: white;
    background-color: rgba(21, 101,192);
    margin: 0px 12px;
    text-align: center;
    height: 25vmin;
}
.infoContainer{
    display: flex;
    align-items: center;
    justify-content: space-around;
}
.logoContainer{
    padding: 0 5%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
}
.alanLogo{
height: 30vmin;
width: 460px;
border-radius: 15%;
padding: 0 5%;
margin: 3% 0;
}
